.sidebar {
    // background-color: lightcoral;
    flex: 1;
    border-right:  0.5px solid lightgray;
    min-height: 100vh;
    background-color:#333;
    .top {
        // background-color: black;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .logo {
        font-size: 20px;
        font-weight: bold;
        color: purple;
    }
    hr {
        height: 0;
        border: 0.5px solid lightgray;
    }
    .Center{
        padding-left: 10px;
    }
    ul {
        list-style: none;
        padding: 0;
        margin: 0;
    }
    .title {
        font-size: 10px;
        font-weight: bold;
        color: gray;
        margin-top: 15px;
        margin-bottom: 5px;
    }
    li {
        display: flex;
        align-items: center;
        padding: 5px;
        cursor: pointer;

        &:hover{
          background-color: #ece8ff;
        }
        .icon {
            font-size: 18px;
            color: #7451f8;
        }
        span {
            font-size: 13px;
            font-weight: 600;
            color: #888;
            margin: 10px;
        }
    }
    .bottom {
        display: flex;
        align-items: center;
        margin: 10px;
        .coloroption{
            width: 20px;
            height: 20px;
            border-radius: 5px;
            border: 1px solid #7451f8;
            cursor: pointer;
            margin: 5px;

            &:nth-child(1){
                background-color: whitesmoke;
            }
            &:nth-child(2){
                background-color: #333;
            }
            &:nth-child(3){
                background-color: darkblue;
            }
           
        }
    }
}