.new {
     width: 100%;
    display: flex;
    // background-color: black;
    .newContainer {
        flex: 6;
        // background-color: black;
        .top,.botto {
            -webkit-box-shadow: 2px 4px 10px 1px rgba(0,0,0,0.47);
             box-shadow: 2px 4px 10px 1px rgba(201,201,201,0.47);
             padding: 10px;
             margin: 20px;
             display: flex;

             h1 {
                color: lightgray;
             }
             .left{
                flex: 1;
                text-align: center;
                img {
                    width: 100px;
                    height: 100px;
                    border-radius: 50%;
                    object-fit: cover;
                }
             }
             .right {
                flex: 2;
                form {
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: space-between;
                    gap: 20px;
                    .formInput {
                        width: 40%;
                     label {
                        display: flex;
                        align-items: center;
                        gap: 10px;
                        .icon {
                            cursor: pointer;
                        }
                     }   
                        input {
                            width: 100%;
                            padding: 5px;
                            border: none;
                            border-bottom:1px solid gray ;
                        }
                    }
                    button {
                        width: 470px;
                        padding: 10px;
                        border: none;
                        background-color: teal;
                        color: white;
                        font-weight: bold;
                        cursor: pointer;
                        margin-top: 10px;
                    }
                }
             }
        }
    }
}